import React from 'react';

import DIV from '@vl/redata/DIV.macro';
import { Row, Col } from 'antd';
import { Image } from '@uz/unitz-components-web/Image';

import { ctx } from '@vl/redata';
import gstyles from '@vl/gstyles';
import _ from 'lodash';
import styled from 'styled-components';
import { ResponsiveProps } from '@uz/unitz-components-web/Responsive';

const CustomParapraph = styled.div`
  p > p {
    color: ${gstyles.colors.main};
  }
`;

const CustomItem = styled.div`
  .ant-row {
    &:nth-child(odd) {
      flex-direction: row;
      .content {
        text-align: left;
      }
    }
    &:nth-child(even) {
      flex-direction: row-reverse;
      .content {
        text-align: right;
      }
    }
  }
`;

const View10 = () => {
  return (
    <DIV className="content">
      <CustomItem>
        {ctx.get('sectionData.children', []).map((item, index) => {
          return (
            <Row gutter={24} className="mb-4 sm:mb-12 justify-center items-center" key={`item_${index}`}>
              <Col lg={12} md={12} sm={24}>
                <div className="mb-3 sm:mb-0">
                  <Image
                    className="object-cover"
                    size="cover"
                    src={ctx.apply('ctf.findImage', item, 'image', '')}
                    alt={item.id}
                    preview={false}
                  />
                </div>
              </Col>
              <Col className="content" lg={12} md={12} sm={24}>
                <ResponsiveProps
                  xs={{
                    renderer: () => (
                      <div>
                        <h2 className="mb-1 text-left text-xl font-semibold text-main">
                          {_.get(item, 'shortText', '')}
                        </h2>
                        <CustomParapraph>
                          {ctx.apply('ctf.renderHTMLText', { name: _.get(item, 'name', '') }, 'longText.longText', {
                            className: 'text-left text-base mb-4',
                            as: 'p',
                          })}
                        </CustomParapraph>
                      </div>
                    ),
                  }}
                  md={{
                    renderer: () => (
                      <div>
                        <h2 className="mb-8 text-base font-semibold text-main">{_.get(item, 'shortText', '')}</h2>
                        <CustomParapraph>
                          {ctx.apply('ctf.renderHTMLText', { name: _.get(item, 'name', '') }, 'longText.longText', {
                            className: 'text-sm mb-4',
                            as: 'p',
                          })}
                        </CustomParapraph>
                      </div>
                    ),
                  }}
                  lg={{
                    renderer: () => (
                      <div>
                        <h2 className="mb-8 text-4xl font-semibold text-main">{_.get(item, 'shortText', '')}</h2>
                        <CustomParapraph>
                          {ctx.apply('ctf.renderHTMLText', { name: _.get(item, 'name', '') }, 'longText.longText', {
                            className: 'text-base mb-4',
                            as: 'p',
                          })}
                        </CustomParapraph>
                      </div>
                    ),
                  }}
                >
                  {(resProps) => !!resProps && resProps.renderer()}
                </ResponsiveProps>
              </Col>
            </Row>
          );
        })}
      </CustomItem>
    </DIV>
  );
};

export default View10;
